import { Block } from '@ali/4ever-cangjie';
import { Paragraph } from '@ali/4ever-plugin-paragraph';
import { createDict } from '@ali/4ever-utils';
import Heading1 from './heading1';
import Heading2 from './heading2';
import Heading3 from './heading3';
import Heading4 from './heading4';
import Heading5 from './heading5';
import Heading6 from './heading6';

type Heading = Heading1 | Heading2 | Heading3 | Heading4 | Heading5 | Heading6;

export const headingTypes = createDict({
  [Heading1.TYPE]: true,
  [Heading2.TYPE]: true,
  [Heading3.TYPE]: true,
  [Heading4.TYPE]: true,
  [Heading5.TYPE]: true,
  [Heading6.TYPE]: true,
});

const Heading = {
  TYPES: Object.keys(headingTypes),
  Heading1,
  Heading2,
  Heading3,
  Heading4,
  Heading5,
  Heading6,
  isHeading(maybeHeading: any): maybeHeading is Heading {
    return Block.isBlock(maybeHeading) && headingTypes[maybeHeading.type];
  },
  isEmpty(block: Block): boolean {
    return headingTypes[block.type]
      && (block.data.jc || 'left') === 'left'
      && !block.data.ind?.left
      && block.text === '';
  },
  isCallout(maybeCallout: any): maybeCallout is typeof Paragraph | Heading {
    return (
      (Paragraph.isParagraph(maybeCallout) || Heading.isHeading(maybeCallout)) &&
      !!maybeCallout.data.calloutPr
    );
  }
};

export default Heading;
